<template>
  <span>
    <a @click="openImg = id" v-if="show">
      <img
        @error="show = false"
        :height="height"
        :width="width"
        :src="id | getImgUrl"
      />
    </a>
    <a
      v-if="openImg == id"
      @click="openImg = null"
      class="lightbox"
    >
      <img :src="id | getImgUrl">
    </a>
  </span>
</template>

<script>
export default {
  props: {
    id: {
      default: undefined
    },
    height: {
      default: 60
    },
    width: {
      default: 60
    }
  },
  data: () => ({
    openImg: null,
    show: true,
  }),
};
</script>

<style scoped>
.lightbox {
  outline: none;
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  text-align: center;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
}

.lightbox img {
  /** Pad the lightbox image */
  max-width: 90%;
  max-height: 80%;
  margin-top: 2%;
}
</style>